.movies {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    padding: 50px;
    padding-top: 70px;
    width: 100%;
  }
  .movies .movie{
    width:45%;
    background-color: #ffffff;
    display:flex;
    align-items: flex-start;
    justify-content: space-between;
    height: 255px;
    margin-bottom:60px;
    padding:20px;
    border-radius: 5px;
    color:#a9a9a9;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}


.movie img{
    max-width: 30%;
    width:100%;
    position: relative;;
    top:-50px;
    margin-right: 30px;
    box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}


.movie .movie__data *{
    margin-bottom: 5px;
    font-weight: 300;
}

.movie .movie__title{
    font-size:1.2em;
    font-weight: 500;
    color:#2c2c2c;
}

.movie .movie__genres{
    display:flex;
    flex-wrap: wrap;
}

.movie__genres li{
    margin-right:3px;
    font-size:0.795em;
}

.movie__genres li:not(:last-child)::after{
    content:',';
    width:5px;
    height: 5px;
}



@media screen and (max-width: 768px) {
    /* Reset */
    .movie .movie__data * {
        font-size:14px;
    }

    .movies{
        padding:50px 20px;
    }
    .movies .movie{
        width:100%;
        height: fit-content;
    }
    
    .movie .movie__title{
        font-size:1.5em;
    }


}

@media screen and (max-width: 568px){
    .movies .movie{
        flex-direction: column;
        align-items: center;
    }

    .movie img{
        max-width:150px;
        margin-right: 0;;
    }
}